  width: 100px;
  height: 40px;
  margin-left: 75%;
  margin-top: -8%;
}

.ui.tiny.button {
  background: rgb(173, 173, 173);
  font-family: 'Nunito', sans-serif;
  color: #FFFFFF;
  font-size: 15px;
  border-radius: 25%;
